import React, { useEffect, useState, useCallback, memo } from 'react';
import { adminApi } from '../utils/apiClient';
import { toast } from 'react-toastify';
import { debounce } from 'lodash';

// Form Input Component
const FormInput = memo(({ label, name, value, onChange, type = "text", ...props }) => {
    return (
        <div>
            <label className="block text-sm font-medium text-gray-700">{label}</label>
            <input
                type={type}
                name={name}
                value={value || ''}
                onChange={onChange}
                className="mt-1 block w-full border rounded-md shadow-sm p-2"
                {...props}
            />
        </div>
    );
});

// Filter Input Component
const FilterInput = memo(({ name, placeholder, value, onChange }) => {
    return (
        <input
            type="text"
            name={name}
            placeholder={placeholder}
            value={value || ''}
            onChange={onChange}
            className="border rounded-lg px-3 py-2"
        />
    );
});

const UserManagement = () => {
    // States
    const [users, setUsers] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, setTotalPages] = useState(0);
    const [filters, setFilters] = useState({
        email: '',
        phone: '',
        name: ''
    });
    const [showModal, setShowModal] = useState(false);
    const [modalMode, setModalMode] = useState('create');
    const [selectedUser, setSelectedUser] = useState(null);
    const [formData, setFormData] = useState({
        firstname: '',
        lastname: '',
        email: '',
        phone: '',
        gender: 'male',
        password: '',
        roles: ['customer']
    });
    const [showConfirmDelete, setShowConfirmDelete] = useState(false);
    const [userToDelete, setUserToDelete] = useState(null);

    // Constants
    const roleLabels = {
        'superadmin': 'Super Admin',
        'admin': 'Admin',
        'customer': 'Khách hàng'
    };

    const roleColors = {
        'superadmin': 'bg-purple-100 text-purple-800',
        'admin': 'bg-blue-100 text-blue-800',
        'customer': 'bg-green-100 text-green-800'
    };

    const genderLabels = {
        'male': 'Nam',
        'female': 'Nữ',
        'other': 'Khác'
    };

    // Effects
    useEffect(() => {
        fetchUsers();
    }, [currentPage, filters]);

    // API Calls
    const fetchUsers = async () => {
        setLoading(true);
        try {
            const response = await adminApi.getAllUsers({
                page: currentPage,
                limit: 10,
                ...filters
            });
            setUsers(response.data.users);
            setTotalPages(response.data.pagination.totalPages);
        } catch (err) {
            setError('Không thể tải danh sách người dùng');
            toast.error('Không thể tải danh sách người dùng');
        } finally {
            setLoading(false);
        }
    };

    // CRUD Operations
    const handleCreateUser = async (userData) => {
        setLoading(true);
        try {
            await adminApi.createUser(userData);
            setShowModal(false);
            fetchUsers();
            toast.success('Tạo người dùng mới thành công');
        } catch (error) {
            setError(error.message || 'Có lỗi xảy ra khi tạo người dùng');
            toast.error('Không thể tạo người dùng mới');
        } finally {
            setLoading(false);
        }
    };

    const handleUpdateUser = async (userId, userData) => {
        setLoading(true);
        try {
            await adminApi.updateUser(userId, userData);
            setShowModal(false);
            fetchUsers();
            toast.success('Cập nhật người dùng thành công');
        } catch (error) {
            setError(error.message || 'Có lỗi xảy ra khi cập nhật người dùng');
            toast.error('Không thể cập nhật người dùng');
        } finally {
            setLoading(false);
        }
    };

    const handleDeleteUser = async (userId) => {
        setLoading(true);
        try {
            await adminApi.deleteUser(userId);
            setShowConfirmDelete(false);
            fetchUsers();
            toast.success('Xóa người dùng thành công');
        } catch (error) {
            setError(error.message || 'Có lỗi xảy ra khi xóa người dùng');
            toast.error('Không thể xóa người dùng');
        } finally {
            setLoading(false);
        }
    };

    // Event Handlers with useCallback
    const handleInputChange = useCallback((e) => {
        const { name, value } = e.target;
        setFormData(prev => ({
            ...prev,
            [name]: value
        }));
    }, []);

    const debouncedFilter = useCallback(
        debounce((name, value) => {
            setFilters(prev => ({
                ...prev,
                [name]: value
            }));
            setCurrentPage(1);
        }, 300),
        []
    );

    const handleFilterChange = useCallback((e) => {
        const { name, value } = e.target;
        debouncedFilter(name, value);
    }, [debouncedFilter]);

    const handleRoleChange = useCallback((role) => {
        setFormData(prev => ({
            ...prev,
            roles: prev.roles.includes(role)
                ? prev.roles.filter(r => r !== role)
                : [...prev.roles, role]
        }));
    }, []);

    const handleSubmit = useCallback(async (e) => {
        e.preventDefault();

        // Validate form data
        if (!formData.firstname || !formData.lastname || !formData.email || !formData.phone) {
            setError('Vui lòng điền đầy đủ thông tin');
            return;
        }

        // Validate email format
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if (!emailRegex.test(formData.email)) {
            setError('Email không hợp lệ');
            return;
        }

        // Validate phone number format
        const phoneRegex = /^[0-9]{10}$/;
        if (!phoneRegex.test(formData.phone)) {
            setError('Số điện thoại không hợp lệ');
            return;
        }

        // Prepare data for API
        const userData = {
            firstname: formData.firstname,
            lastname: formData.lastname,
            email: formData.email,
            phone: formData.phone,
            gender: formData.gender,
            roles: formData.roles
        };

        // Add password only for create mode
        if (modalMode === 'create') {
            if (!formData.password || formData.password.length < 6) {
                setError('Mật khẩu phải có ít nhất 6 ký tự');
                return;
            }
            userData.password = formData.password;
        }

        try {
            if (modalMode === 'create') {
                await handleCreateUser(userData);
            } else {
                await handleUpdateUser(selectedUser.id, userData);
            }
        } catch (error) {
            console.error('Error submitting form:', error);
        }
    }, [formData, modalMode, selectedUser]);

    const openCreateModal = useCallback(() => {
        setModalMode('create');
        setFormData({
            firstname: '',
            lastname: '',
            email: '',
            phone: '',
            gender: 'male',
            password: '',
            roles: ['customer']
        });
        setShowModal(true);
        setError(null);
    }, []);

    const openEditModal = useCallback((user) => {
        setModalMode('edit');
        setSelectedUser(user);
        setFormData({
            firstname: user.firstname,
            lastname: user.lastname,
            email: user.email,
            phone: user.phone,
            gender: user.gender,
            roles: user.roles,
            password: ''
        });
        setShowModal(true);
        setError(null);
    }, []);

    // Modal Components
    const UserFormModal = useCallback(() => (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
            <div className="relative top-20 mx-auto p-5 border w-96 shadow-lg rounded-md bg-white">
                <div className="mt-3">
                    <h3 className="text-lg font-medium">
                        {modalMode === 'create' ? 'Thêm người dùng mới' : 'Cập nhật người dùng'}
                    </h3>
                    <form onSubmit={handleSubmit} className="mt-4">
                        {error && (
                            <div className="mb-4 p-3 bg-red-100 border border-red-400 text-red-700 rounded">
                                {error}
                            </div>
                        )}

                        <div className="grid gap-4">
                            <FormInput
                                label="Tên"
                                name="firstname"
                                value={formData.firstname}
                                onChange={handleInputChange}
                                required
                            />

                            <FormInput
                                label="Họ"
                                name="lastname"
                                value={formData.lastname}
                                onChange={handleInputChange}
                                required
                            />

                            <FormInput
                                label="Email"
                                type="email"
                                name="email"
                                value={formData.email}
                                onChange={handleInputChange}
                                required
                            />

                            <FormInput
                                label="Số điện thoại"
                                name="phone"
                                value={formData.phone}
                                onChange={handleInputChange}
                                required
                            />

                            <div>
                                <label className="block text-sm font-medium text-gray-700">Giới tính</label>
                                <select
                                    name="gender"
                                    value={formData.gender}
                                    onChange={handleInputChange}
                                    className="mt-1 block w-full border rounded-md shadow-sm p-2"
                                >
                                    {Object.entries(genderLabels).map(([value, label]) => (
                                        <option key={value} value={value}>{label}</option>
                                    ))}
                                </select>
                            </div>

                            {modalMode === 'create' && (
                                <FormInput
                                    label="Mật khẩu"
                                    type="password"
                                    name="password"
                                    value={formData.password}
                                    onChange={handleInputChange}
                                    required
                                    minLength="6"
                                />
                            )}

                            <div>
                                <label className="block text-sm font-medium text-gray-700">Vai trò</label>
                                <div className="mt-2 space-y-2">
                                    {Object.entries(roleLabels).map(([role, label]) => (
                                        <label key={role} className="flex items-center">
                                            <input
                                                type="checkbox"
                                                checked={formData.roles.includes(role)}
                                                onChange={() => handleRoleChange(role)}
                                                className="mr-2"
                                            />
                                            <span>{label}</span>
                                        </label>
                                    ))}
                                </div>
                            </div>
                        </div>

                        <div className="mt-6 flex justify-end space-x-3">
                            <button
                                type="button"
                                onClick={() => {
                                    setShowModal(false);
                                    setError(null);
                                }}
                                className="px-4 py-2 border text-gray-700 rounded-md hover:bg-gray-50"
                            >
                                Hủy
                            </button>
                            <button
                                type="submit"
                                disabled={loading}
                                className="px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600
                                    disabled:opacity-50 disabled:cursor-not-allowed"
                            >
                                {loading ? 'Đang xử lý...' : (modalMode === 'create' ? 'Thêm' : 'Cập nhật')}
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    ), [formData, modalMode, error, loading, handleInputChange, handleSubmit, handleRoleChange]);

    const DeleteConfirmModal = useCallback(() => (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
            <div className="relative top-20 mx-auto p-5 border w-96 shadow-lg rounded-md bg-white">
                <h3 className="text-lg font-medium mb-4">Xác nhận xóa</h3>
                <p>Bạn có chắc chắn muốn xóa người dùng này?</p>
                <div className="mt-4 flex justify-end space-x-2">
                    <button
                        onClick={() => setShowConfirmDelete(false)}
                        className="px-4 py-2 bg-gray-200 text-gray-800 rounded hover:bg-gray-300"
                    >
                        Hủy
                    </button>
                    <button
                        onClick={() => handleDeleteUser(userToDelete)}
                        className="px-4 py-2 bg-red-500 text-white rounded hover:bg-red-600"
                        disabled={loading}
                    >
                        {loading ? 'Đang xử lý...' : 'Xóa'}
                    </button>
                </div>
            </div>
        </div>
    ), [loading, userToDelete]);

    if (loading && !users.length) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
            </div>
        );
    }

    return (
        <div className="p-6 max-w-full mx-auto">
            {/* Header */}
            <div className="mb-6">
                <div className="flex justify-between items-center mb-4">
                    <h2 className="text-2xl font-bold text-gray-800">Quản lý người dùng</h2>
                    <button
                        onClick={openCreateModal}
                        className="px-4 py-2 bg-green-500 text-white rounded hover:bg-green-600"
                    >
                        Thêm người dùng
                    </button>
                </div>

                {/* Filters */}
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    <FilterInput
                        name="email"
                        placeholder="Tìm theo email"
                        value={filters.email}
                        onChange={handleFilterChange}
                    />
                    <FilterInput
                        name="phone"
                        placeholder="Tìm theo số điện thoại"
                        value={filters.phone}
                        onChange={handleFilterChange}
                    />
                    <FilterInput
                        name="name"
                        placeholder="Tìm theo tên"
                        value={filters.name}
                        onChange={handleFilterChange}
                    />
                </div>
            </div>

            {/* Table */}
            <div className="bg-white rounded-lg shadow overflow-x-auto">
                <table className="min-w-full divide-y divide-gray-200">
                    <thead className="bg-gray-50">
                        <tr>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">ID</th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Họ tên</th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Email</th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Số điện thoại</th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Giới tính</th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Vai trò</th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Thao tác</th>
                        </tr>
                    </thead>
                    <tbody className="bg-white divide-y divide-gray-200">
                        {users.map((user) => (
                            <tr key={user.id}>
                                <td className="px-6 py-4 whitespace-nowrap">#{user.id}</td>
                                <td className="px-6 py-4 whitespace-nowrap">
                                    {user.firstname} {user.lastname}
                                </td>
                                <td className="px-6 py-4 whitespace-nowrap">{user.email}</td>
                                <td className="px-6 py-4 whitespace-nowrap">{user.phone}</td>
                                <td className="px-6 py-4 whitespace-nowrap">
                                    {genderLabels[user.gender]}
                                </td>
                                <td className="px-6 py-4 whitespace-nowrap">
                                    <div className="flex flex-wrap gap-1">
                                        {user.roles.map((role) => (
                                            <span
                                                key={role}
                                                className={`px-2 py-1 text-xs rounded-full ${roleColors[role]}`}
                                            >
                                                {roleLabels[role]}
                                            </span>
                                        ))}
                                    </div>
                                </td>
                                <td className="px-6 py-4 whitespace-nowrap">
                                    <div className="flex space-x-2">
                                        <button
                                            onClick={() => openEditModal(user)}
                                            className="text-blue-600 hover:text-blue-900"
                                        >
                                            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2"
                                                    d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z" />
                                            </svg>
                                        </button>
                                        <button
                                            onClick={() => {
                                                setUserToDelete(user.id);
                                                setShowConfirmDelete(true);
                                            }}
                                            className="text-red-600 hover:text-red-900"
                                        >
                                            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2"
                                                    d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                                            </svg>
                                        </button>
                                    </div>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>

            {/* Pagination */}
            {totalPages > 1 && (
                <div className="mt-4 flex justify-between items-center">
                    <div>
                        Trang {currentPage} / {totalPages}
                    </div>
                    <div className="flex space-x-2">
                        <button
                            onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
                            disabled={currentPage === 1}
                            className={`px-4 py-2 rounded ${currentPage === 1
                                    ? 'bg-gray-100 text-gray-400'
                                    : 'bg-blue-500 text-white hover:bg-blue-600'
                                }`}
                        >
                            Trước
                        </button>
                        <button
                            onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
                            disabled={currentPage >= totalPages}
                            className={`px-4 py-2 rounded ${currentPage >= totalPages
                                    ? 'bg-gray-100 text-gray-400'
                                    : 'bg-blue-500 text-white hover:bg-blue-600'
                                }`}
                        >
                            Sau
                        </button>
                    </div>
                </div>
            )}

            {/* Modals */}
            {showModal && <UserFormModal />}
            {showConfirmDelete && <DeleteConfirmModal />}
        </div>
    );
};

export default memo(UserManagement);
